/**
 * 
 * @param {Object} params  {objectid:2485094,objecttype:'trend',page:1,count:20}
 * objecttype album trend topicreply video 
 * objectid IDObjectID
 */
export async function getTopList(params = {
  objectid: 2485094,
  objecttype: 'trend',
  page: 1,
  count: 20
}) {
  return await request('/Interact/GetTopList', 'get', params)
}

