/**
 * 
 * @param {String} pStr 
 * @param {Number} pLen 
 * @return {Object} 
 */
export function fnCutString(pStr, pLen) {
  // 
  let _strLen = pStr.length;
  var _cutString = "";
  // 
  var _cutFlag = true;
  var _lenCount = 0;
  var _ret = false;
  if (_strLen <= pLen / 2) {
    _cutString = pStr;
    _ret = true;
  }
  if (!_ret) {
    for (var i = 0; i < _strLen; i++) {
      if (pStr.charAt(i).charCodeAt(0) > 128) {
        _lenCount += 2;
      } else {
        _lenCount += 1;
      }

      if (_lenCount > pLen) {
        _cutString = pStr.substring(0, i);
        _ret = true;
        break;
      } else if (_lenCount == pLen) {
        _cutString = pStr.substring(0, i + 1);
        _ret = true;
        break;
      }
    }
  }

  if (!_ret) {
    _cutString = pStr;
    _ret = true;
  }

  if (_cutString.length == _strLen) {
    _cutFlag = false;
  }

  return {
    "cutstring": _cutString,
    "cutflag": _cutFlag
  };
}

/**
 *  4
 * @param {Date} time 
 * @return {Object} 
 */
export function fnFormatDate(time) {
  var ago, curTime, diff, int;
  time -= 0;
  if (("" + time).length === 10) {
    time *= 1000;
  }
  int = parseInt;
  curTime = +new Date();
  diff = curTime - time;
  ago = "";
  if (1000 * 60 > diff) {
    ago = "";
  } else if (1000 * 60 <= diff && 1000 * 60 * 60 > diff) {
    ago = int(diff / (1000 * 60)) + "";
  } else if (1000 * 60 * 60 <= diff && 1000 * 60 * 60 * 24 > diff) {
    ago = int(diff / (1000 * 60 * 60)) + "";
  } else if (1000 * 60 * 60 * 24 <= diff && 1000 * 60 * 60 * 24 * 30 > diff) {
    ago = int(diff / (1000 * 60 * 60 * 24)) + "";
  } else if (1000 * 60 * 60 * 24 * 30 <= diff && 1000 * 60 * 60 * 24 * 30 * 12 > diff) {
    ago = int(diff / (1000 * 60 * 60 * 24 * 30)) + "";
  } else {
    ago = int(diff / (1000 * 60 * 60 * 24 * 30 * 12)) + "";
  }
  return ago;
}

/**
 *  2019-12-03 20:12
 * @param {Date} time 
 * @return {Object} 
 */
export function fnFormatLocalDate(time) {
  time = new Date(+time);
  let year = time.getUTCFullYear(); //
  let month = time.getUTCMonth() + 1; //
  let day = time.getDate(); //
  let hh = time.getHours(); //
  let mm = time.getUTCMinutes(); // 
  // 
  month = month < 10 ? "0" + month : month
  day = day < 10 ? "0" + day : day
  hh = hh < 10 ? "0" + hh : hh
  mm = mm < 10 ? "0" + mm : mm
  return `${year}-${month}-${day} ${hh}:${mm}`;
}


/**
 *   08:12 |  09:12 | 2019-12-03 20:12
 * @param {Date} time Unix
 * @return {Object} 
 */
 export function fnFormatTimeHeader(time) {
   // 
   let date = new Date(parseInt(time)),
     year = date.getUTCFullYear(),
     month = date.getUTCMonth(),
     day = date.getDate(),
     hour = date.getHours(),
     minute = date.getUTCMinutes()
   // 
   let currentDate = new Date(),
     currentYear = date.getUTCFullYear(),
     currentMonth = date.getUTCMonth(),
     currentDay = currentDate.getDate()
   // 
   if (currentYear == year && currentMonth == month && currentDay == day) { //
     if (hour > 12) {
       return ` ${hour}:${minute < 10 ? '0' + minute : minute}`
     } else {
       return ` ${hour}:${minute < 10 ? '0' + minute : minute}`
     }
   }
   // 
   let yesterday = new Date(currentDate - 24 * 3600 * 1000)
   if (year == yesterday.getUTCFullYear() && month == yesterday.getUTCMonth && day == yesterday.getDate()) { //
     return ` ${hour}:${minute < 10 ? '0' + minute : minute}`
   } else {
     return `${year}-${month + 1}-${day} ${hour}:${minute < 10 ? '0' + minute : minute}`
   }
 }
 
/**
 *  06:28:43 
 * @param {Number} time  23323
 * @return {Object} 
 */
export function fnSecondToTime(time) {
  var hour = 0;
  var minute = 0;
  var second = 0;
  if (time >= 60) {
    minute = Math.floor(time / 60);
    if (minute >= 60) {
      hour = Math.floor(minute / 60);
      minute = minute % 60;
    }
  }
  second = time % 60;
  // 
  hour = hour < 10 ? "0" + hour : hour
  minute = minute < 10 ? "0" + minute : minute
  second = second < 10 ? "0" + second : second
  return `${hour}${minute}${second}`;
}
