/**
 * ===========
 * 
 * ===========
 */
export default function request(route, method = 'get', data = {}) {
  return new Promise((resolve, reject) => {
    uni.request({
      // url: 'http://api5.laosha.net' + route,
      url: 'http://easy-mock.liuup.com/mock/5df764250a2f9f42cfec1a50/api5.hanfugou.com' + route,
      method,
      data,
      header: {
        'hanfuhui_version': 3,
        'hanfuhui_token': uni.getStorageSync('TOKEN') || '',
        'hanfuhui_fromclient': 'android',
        'content-type': 'application/x-www-form-urlencoded',
      },
      success: res => {
        // console.log('', res);
        // token
        if (res.statusCode == 401 || res.data.ErrorMessage == '') {
          reject({
            data: 401,
            status: res.statusCode
          })
          uni.redirectTo({
            url: '/pages/login/login'
          })
          return;
        }
        // 
        if (res.statusCode == 500) {
          reject({
            data: {},
            status: res.statusCode
          })
          uni.showToast({
            title: '',
            icon: 'none'
          })
          return;
        }
        // 
        if (res.data.ErrorMessage != '') {
          reject({
            data: res.data,
            status: res.statusCode
          })
          uni.showToast({
            title: res.data.ErrorMessage,
            icon: 'none'
          })
          return;
        }
        // 
        resolve({
          data: res.data,
          status: res.statusCode
        })
      },
      fail: err => {
        // console.log(err)
        reject(err)
        uni.showToast({
          title: '',
          icon: 'none'
        })
      }
    })
  })
}
