import request from '@/api/request.js';
/**
 * ===========
 * 
 * ===========
 */


/**
 * 
 * @param {Object} params  {page:1,maxid:-1,count:20}
 */
export async function getSquareList(params = {
  page: 1,
  maxid: -1,
  count: 20
}) {
  return await request('/trend/GetTrendListForSquare', 'get', params)
}

